* {
  margin: 0;
  box-sizing: border-box;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.dashboard {
  text-align: center;
}

.header {
  background-color: #f8b43d;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  text-transform: capitalize;
  color: white;
  box-shadow: 2px 2px 2px #d9d9d9;
}

.errorMessage {
  margin: auto;
  font-weight: bold;
  color: rgb(161, 15, 15);
}

.search {
  margin: 20px;
}

.textInput {
  height: 25px;
}

input[type="submit"] {
  padding: 5px;
  background-color: #f8b43d;
  color: white;
  font-size: 13px;
  font-family: Helvetica, sans-serif;
  width: 80px;
  height: 30px;
  margin-left: 5px;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 2px #d9d9d9;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: #282c34;
  color: antiquewhite;
}

.search > input[type="text"]{
  width: 40%;
  min-width: 170px;
  height: 30px;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 2px #d9d9d9;
  padding-left: 5px;
  font-family: Helvetica, sans-serif;
  font-size: 13px;
}

.thumbnails {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
}

.event {
  padding: 5px 25px 10px 25px;
  width: 40%;
  min-height: 300px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 2px #d9d9d9;
}

.title {
  font-size: 18px;
}

.image {
  width: 200px;
  margin: 10px 0 10px 0;
}

.button {
  background-color: #f8b43d;
  height: 30px;
  width: 80px;
}

.link {
  text-decoration: none;
  color: white;
  font-family: Helvetica, sans-serif;
}

@media screen and (max-width: 600px) {
  .event {
    width: 80%;
  }
}